	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"/363\">YASiM-CITY</a>の親(?)サイトとして、なんでもありサイト“YASiM-CITY solo”を開設。サイト名が余り親じゃないのはご愛嬌。初心者向けPerl解説とか、<a href=\"/declined\">寂れたBBS同盟</a>とか、果ては事業化計画(主に PC やガラケー向けの静的 Web 制作)とかくだらないことも結構やってた。  初期は VirtualAvenue、後期は初の有料レンタルサーバ AHOPE に移転。 その後大学サークル活動でシャレにならないほど忙しくて冬までほぼ放置状態だった。 5～12PV/day。背景色薄赤(のちに薄青紫→白)。この後に白基調のWebページを作ったのは、実に10年後。 どうでもいいが、この辺から自作サイトのコンテンツファイルをほぼ全部ローカルに保存している。今も持ってて、時折見ては懐かしんでいる自分が。皆さんも真似してみてはいかがでしょうか(ぉ</p>\n<h2>“仮想都市FCoV”</h2>\n<p>当時の屋号 “空想都市計画” の基となった、ウェブページ上に存在する仮想の都市。都市を模した静的なWebコンテンツに、人工無能チャットや掲示板などを埋め込んでコミュニケーションを活性化させる、という構想を持っていた。なお技術力はせいぜいアクセスカウンタやごく簡単な掲示板が作成できる程度で、オブジェクト指向すら知らなかった程度の能力のため、結局ごく簡単なプロトタイプが出来上がるにとどまった。</p>","frontmatter":{"cover":null,"date":"2000-04-20T00:00:49+09:00","strDate":"2000/4/19","title":"YASiM-CITY Solo/空想都市計画"}}},"pathContext":{"path":"/ja/blog/2000/04/20-kuto/","langKey":"ja"}}

